<template>
  <div>
    <p></p>
    <p class="pTwo">
      "{{dataList.must[1].field|zhFieldVal}}" 
      "{{dataList.must[1].value}}"</p>
    <div class="rankedDistrbution">
      <h5>{{total}}()</h5>
      
      <el-progress :text-inside="true" :stroke-width="16"
                   :percentage="this.precents.emergencyData" color="#CC092F">
      </el-progress>
      
      <el-progress :text-inside="true" :stroke-width="16"
                   :percentage="this.precents.alertData" color="#F37043">
      </el-progress>
      
      <el-progress :text-inside="true" :stroke-width="16"
                   :percentage="this.precents.criticalData" color="#ECDE29">
      </el-progress>
      
      <el-progress :text-inside="true" :stroke-width="16"
                   :percentage="this.precents.errorData" color="#00A8E1">
      </el-progress>
      
      <el-progress :text-inside="true" :stroke-width="16"
                   :percentage="this.precents.noticeData"
                   color="#73AE57"></el-progress>
    </div>
  </div>
</template>

<script>
  const ruleList = {
    '@timestamp': '',
    time: '',
    'container.id': 'ID',
    'container.name': '',
    priority: '',
    rule: '',
    output: '',
    hostName: ''
  }
  export default {
    props: ['dataList', 'total', 'priorityPrecent'],
    data() {
      return {
        noticeData: [0, 0, 0]
      }
    },
    computed: {
      precents() {
        const [alertData, criticalData, emergencyData,
          errorData, ...noticeData] = this.priorityPrecent
        return {
          emergencyData,
          alertData,
          errorData,
          criticalData,
          noticeData: noticeData.reduce((pre, next) => pre + next)
        }
      }
    },
    filters: {
      zhFieldVal(fieldVal) {
        return ruleList[fieldVal] || []
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  p
    font-family ''
    font-size 15px

  .pTwo
    font-size 13px

  .rankedDistrbution
    background-color #fff
    margin 9px 25px 20px 0
    padding 9px 40px 20px 20px
</style>



