<template>
  <div>
    <a></a>
    <div class="edit-rules">
      <el-tooltip class="item" effect="dark" content="" placement="top">
        <el-button @click="handleSettingRules" type="text" size="small" class="setting-btn">
          <i class="iconfont icon-setting">&#xe60e;</i>
        </el-button>
      </el-tooltip>
      <el-dialog title="" :visible.sync="dialogVisible"
                 width="50%" :before-close="handleClose">
        <el-tabs v-model="activeName" type="card">
          <el-tab-pane label="" name="documentName">
            <el-tabs v-model="documentName" @tab-click="handleGetSinRules">
              <el-tab-pane label="" value="unwritable_files" name="unwritable_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="" value="unread_files" name="unread_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="" value="undelete_files" name="undelete_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="" value="unrename_files" name="unrename_files">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="" value="uncreated_dirs" name="uncreated_dirs">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="" name="containerName">
            <el-tabs v-model="containerName" @tab-click="handleGetSinRules">
              <el-tab-pane label="K8S" name="first">K8S</el-tab-pane>
              <el-tab-pane label="" value="unlogin_containers" name="unlogin_containers">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="" name="userName">
            <el-tabs v-model="userName" @tab-click="handleGetSinRules">
              <el-tab-pane label="" name="first"></el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="" name="procedureName">
            <el-tabs v-model="procedureName" @tab-click="handleGetSinRules">
              <el-tab-pane label="" name="first"></el-tab-pane>
              <el-tab-pane label="" name="second"></el-tab-pane>
              <el-tab-pane label="" value="user_soft_mgmts" name="user_soft_mgmts">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="shell" value="user_trusted_shells" name="user_trusted_shells">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="" name="fifth"></el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="" name="databaseName">
            <el-tabs v-model="databaseName" @tab-click="handleGetSinRules">
              <el-tab-pane label="" name="first"></el-tab-pane>
              <el-tab-pane label="" name="second"></el-tab-pane>
            </el-tabs>
          </el-tab-pane>
          <el-tab-pane label="" name="networkName">
            <el-tabs v-model="networkName" @tab-click="handleGetSinRules">
              <el-tab-pane label="" value="user_container_ports" name="user_container_ports">
                <Table :rulesNameItem="rulesNameItem" :tab="tabName"></Table>
              </el-tab-pane>
              <el-tab-pane label="SSH" name="second">SSH</el-tab-pane>
            </el-tabs>
          </el-tab-pane>
        </el-tabs>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
         </span>
      </el-dialog>
    </div>
    <el-table class="el-table" :data="allRulesList"
              @cell-click="handleFirstAndSecondTitle">
      <el-table-column type="index" width="20">
      </el-table-column>
      <el-table-column prop="_source.rulename" label=""
                       show-overflow-tooltip header-align="center" min-width="40">
        <template slot-scope="props">
          <i class="rule-name-class">{{props.row._source.rulename}}</i>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import * as apiRules from '@/api/monitorRules'

  import Table from './rulesTable/index'

  const ruleList = new Map()
    .set('unwritable_files', 'documentName')
    .set('unread_files', 'documentName')
    .set('undelete_files', 'documentName')
    .set('unrename_files', 'documentName')
    .set('uncreated_dirs', 'documentName')
    .set('unlogin_containers', 'containerName')
    .set('user_soft_mgmts', 'procedureName')
    .set('user_trusted_shells', 'procedureName')
    .set('user_container_ports', 'networkName')

  export default {
    components: {
      Table
    },
    data() {
      return {
        allRulesList: '',
        secondTitle: '',
        rulesNameItem: '',
        dialogVisible: false,
        activeName: 'documentName',
        documentName: 'unwritable_files',
        containerName: 'unlogin_containers',
        userName: 'first',
        procedureName: 'first',
        databaseName: 'first',
        networkName: 'first',
        tabName: ''
      }
    },
    methods: {
      // 
      getFirstTitle(secondTitle) {
        return ruleList.get(secondTitle) || ''
      },
      GetSinRules(secondTitle) {
        apiRules.getSinRules(secondTitle).then(res => {
          this.rulesNameItem = res.data.items || []
        })
      },
      // 
      handleSettingRules() {
        this.dialogVisible = true
        this.activeName = 'documentName'
        this.documentName = 'unwritable_files'
        this.GetSinRules('unwritable_files')
      },
      // 
      handleGetSinRules(tab) {
        this.tabName = tab.$attrs.value
        apiRules.getSinRules(tab.$attrs.value).then(res => {
          this.rulesNameItem = res.data.items || []
        })
      },
      // ,
      getSecondTitle(activeName) {
        switch (activeName) {
          case 'documentName':
            this.documentName = this.secondTitle
            break
          case 'containerName':
            this.containerName = this.secondTitle
            break
          case 'userName':
            this.userName = this.secondTitle
            break
          case 'procedureName':
            this.procedureName = this.secondTitle
            break
          case 'databaseName':
            this.databaseName = this.secondTitle
            break
          default:
            this.networkName = this.secondTitle
        }
      },
      // 
      handleFirstAndSecondTitle(row) {
        this.dialogVisible = true
        // eslint-disable-next-line
        this.secondTitle = row._id
        this.activeName = this.getFirstTitle(this.secondTitle)
        this.getSecondTitle(this.activeName)
        this.GetSinRules(this.secondTitle)
      },
      // 
      handleClose() {
        this.dialogVisible = true
      }
    },
    // 
    created() {
      apiRules.getAllRules().then(allRules => {
        this.allRulesList = allRules.data
      })
    }
  }
</script>

<style lang="stylus" scoped>
  a
    font-family ''
    font-size 15px

  .edit-rules
    margin: -25px 0 0 60px

  .el-table
    width 93%

  .setting-btn
    width 100px
    font-size 14px
    float: right

  .rule-name-class
    cursor pointer
</style>
<style lang="stylus">
  .el-dialog__headerbtn .el-dialog__close
    display none
</style>



